.containerFooter{
    width: 100%;
    height: 226px;
    background: #00739D;
    display: flex;
}
.leftFooter{
    width: 32%;
    text-align: center;
    align-self: center;
}
.centerFooter{
    width: 28%;
    text-decoration:inherit;
    align-items: center;
    align-self: center;
    cursor: pointer;
    font-family: 'Poppins';
    font-style: normal;
    font-size: 2.5rem;
    line-height: 24px;
    /* identical to box height */
    
    text-decoration-line: underline;
    
    color: #FFFFFF;
}
.rigthFooter{
    width:40%;
    display: flex;
    align-self: center;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 4rem;
    line-height: 30px;

    color: #FFFFFF;
}
.rigthFooter a{
    margin-left: 5%;
}
.imgFooterInstagram{
    margin-left: 5%;
    width: 46px;
    height: 46px;
    cursor: pointer;
}
.imgFooterWpp{
    width: 46px;
    height: 46px;
    margin-left: 5%;
    cursor: pointer;
}
.mainTyCFooter{
    text-decoration:inherit;
    font-family: 'Poppins';
    font-style: normal;
    font-size: 2.5rem;
    color: #FFFFFF;
}